.settings-container {
  max-width: 480px;
  margin: 2.5rem auto 1.5rem auto;
  padding: 1.5rem 1.2rem;
  background: rgba(255,255,255,0.98);
  border-radius: 18px;
  box-shadow: 0 6px 24px rgba(46,204,113,0.13), 0 1.5px 6px rgba(0,0,0,0.06);
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  color: #333;
}

.settings-form {
  display: flex;
  flex-direction: column;
  gap: 0.7rem;
}

.settings-form h2 {
  color: #27ae60;
  font-size: 1.5rem;
  font-weight: 800;
  margin-bottom: 0.7rem;
  text-align: center;
}

.settings-form label {
  display: flex;
  flex-direction: row;
  align-items: center;
  font-weight: 600;
  color: #2ecc71;
  font-size: 1rem;
  margin-bottom: 0.2rem;
  gap: 0.7rem;
}

.settings-form label input[type="text"],
.settings-form label input[type="email"],
.settings-form label input[type="password"],
.settings-form label select,
.settings-form label input[type="time"] {
  flex: 1;
  padding: 0.4rem 0.7rem;
  border-radius: 7px;
  border: 1.2px solid #b2f2d6;
  font-size: 1rem;
  background: #f7f7f7;
  color: #222;
  margin-left: 0.5rem;
  transition: border 0.2s;
}

.settings-form label input[type="text"]:focus,
.settings-form label input[type="email"]:focus,
.settings-form label input[type="password"]:focus,
.settings-form label select:focus,
.settings-form label input[type="time"]:focus {
  border: 1.2px solid #2ecc71;
  outline: none;
}

.checkbox-label {
  display: flex;
  flex-direction: row;
  align-items: center;
  font-weight: 600;
  color: #27ae60;
  font-size: 0.98rem;
  gap: 0.5rem;
  margin-bottom: 0.1rem;
}

.checkbox-label input[type="checkbox"] {
  accent-color: #2ecc71;
  width: 16px;
  height: 16px;
  margin-right: 0.2rem;
}

.buttons-row {
  display: flex;
  gap: 0.7rem;
  justify-content: center;
  margin-top: 0.7rem;
}

.btn-green, .btn-red {
  border-radius: 7px;
  padding: 0.5rem 1.3rem;
  font-size: 1rem;
  font-weight: 700;
  border: none;
  cursor: pointer;
  transition: background 0.2s, transform 0.2s;
}

.btn-green {
  background: linear-gradient(90deg, #2ecc71 60%, #27ae60 100%);
  color: #fff;
  box-shadow: 0 1.5px 6px #b2f2d6;
}

.btn-green:hover {
  background: linear-gradient(90deg, #27ae60 60%, #2ecc71 100%);
  transform: translateY(-1px) scale(1.03);
}

.btn-red {
  background: linear-gradient(90deg, #fc5c7d 60%, #6a82fb 100%);
  color: #fff;
  box-shadow: 0 1.5px 6px #f8bbd0;
}

.btn-red:hover {
  background: linear-gradient(90deg, #6a82fb 60%, #fc5c7d 100%);
  transform: translateY(-1px) scale(1.03);
}

.message {
  margin-top: 0.7rem;
  color: #27ae60;
  font-weight: 700;
  text-align: center;
  font-size: 1rem;
}

@media (max-width: 600px) {
  .settings-container {
    padding: 0.7rem 0.1rem;
  }
  .settings-form h2 {
    font-size: 1.1rem;
  }
  .settings-form label {
    font-size: 0.95rem;
  }
}